import React, { useEffect, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Eye, DollarSign, ArrowDownLeft, ArrowRightLeft, Calendar, Clock } from 'lucide-react';
import { ProfitService } from '@/utils/profitService';
import { reportsService } from '@/utils/reportsService';
import { MasterPinDialog } from './MasterPinDialog';
import { toast } from '@/hooks/use-toast';
import { getUserSubscription, SubscriptionPlan } from '@/utils/subscriptionService';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';

interface TotalProfitDisplayDialogProps {
  userId?: string;
  transactions?: any[];
}

interface TotalsData {
  dailyWithdraw: number;
  dailyTransfer: number;
  monthlyWithdraw: number;
  monthlyTransfer: number;
  lastUpdated: Date;
}

const TotalProfitDisplayDialog: React.FC<TotalProfitDisplayDialogProps> = ({ userId, transactions = [] }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isMasterPinDialogOpen, setIsMasterPinDialogOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [totals, setTotals] = useState<TotalsData>({
    dailyWithdraw: 0,
    dailyTransfer: 0,
    monthlyWithdraw: 0,
    monthlyTransfer: 0,
    lastUpdated: new Date(),
  });
  const { userSubscription } = useFirebaseAuth();

  useEffect(() => {
    if (userId && isOpen && isAuthenticated) {
      loadTotals();
    }
  }, [userId, isOpen, isAuthenticated]);

  const formatCurrency = (amount: number) => `${amount.toFixed(2)} .`;

  const isZeroPlan = async (): Promise<boolean> => {
    try {
      const localSub: any = (userSubscription as any)?.subscription || null;
      const rawLocal: any = localSub?.planType ?? localSub?.plan ?? null;
      const planLocalStr = typeof rawLocal === 'string' ? rawLocal.toLowerCase() : null;
      if (rawLocal === SubscriptionPlan.ZERO || planLocalStr === 'zero' || planLocalStr === '0' || rawLocal === 0) {
        return true;
      }
    } catch {}

    try {
      if (userId) {
        const remoteSub: any = await getUserSubscription(userId);
        const rawRemote: any = remoteSub?.planType ?? remoteSub?.plan ?? null;
        const planRemoteStr = typeof rawRemote === 'string' ? rawRemote.toLowerCase() : null;
        if (rawRemote === SubscriptionPlan.ZERO || planRemoteStr === 'zero' || planRemoteStr === '0' || rawRemote === 0) {
          return true;
        }
      }
    } catch {}

    return false;
  };

  const handleEyeClick = async () => {
    try {
      const isTahweela = await (async () => {
        try {
          const localSub: any = (userSubscription as any)?.subscription || null;
          const rawLocal: any = localSub?.planType ?? localSub?.plan ?? null;
          const planLocalStr = typeof rawLocal === 'string' ? rawLocal.toLowerCase() : null;
          if (rawLocal === SubscriptionPlan.TAHWEELA || planLocalStr === 'tahweela') return true;
        } catch {}
        try {
          if (userId) {
            const remoteSub: any = await getUserSubscription(userId);
            const rawRemote: any = remoteSub?.planType ?? remoteSub?.plan ?? null;
            const planRemoteStr = typeof rawRemote === 'string' ? rawRemote.toLowerCase() : null;
            if (rawRemote === SubscriptionPlan.TAHWEELA || planRemoteStr === 'tahweela') return true;
          }
        } catch {}
        return false;
      })();
      if (await isZeroPlan() || isTahweela) {
        toast({
          title: '  ',
          description: '       .',
          variant: 'destructive',
        });
        return;
      }
    } catch {}
    setIsMasterPinDialogOpen(true);
  };

  const handlePinSuccess = async () => {
    try {
      const isTahweela = await (async () => {
        try {
          const localSub: any = (userSubscription as any)?.subscription || null;
          const rawLocal: any = localSub?.planType ?? localSub?.plan ?? null;
          const planLocalStr = typeof rawLocal === 'string' ? rawLocal.toLowerCase() : null;
          if (rawLocal === SubscriptionPlan.TAHWEELA || planLocalStr === 'tahweela') return true;
        } catch {}
        try {
          if (userId) {
            const remoteSub: any = await getUserSubscription(userId);
            const rawRemote: any = remoteSub?.planType ?? remoteSub?.plan ?? null;
            const planRemoteStr = typeof rawRemote === 'string' ? rawRemote.toLowerCase() : null;
            if (rawRemote === SubscriptionPlan.TAHWEELA || planRemoteStr === 'tahweela') return true;
          }
        } catch {}
        return false;
      })();
      if (await isZeroPlan() || isTahweela) {
        toast({
          title: '  ',
          description: '       .',
          variant: 'destructive',
        });
        setIsMasterPinDialogOpen(false);
        return;
      }
    } catch {}

    setIsAuthenticated(true);
    setIsOpen(true);
    setIsMasterPinDialogOpen(false);
  };

  const loadTotals = async () => {
    if (!userId) return;

    try {
      if (Array.isArray(transactions) && transactions.length > 0) {
        const today = new Date();
        const currentMonth = today.getMonth();
        const currentYear = today.getFullYear();

        const visibleDaily = reportsService.filterVisibleTransactions(transactions as any[], 'daily', userId);
        const visibleMonthly = reportsService.filterVisibleTransactions(transactions as any[], 'monthly', userId);

        let dailyWithdrawProfit = 0;
        let dailyTransferProfit = 0;
        let monthlyWithdrawProfit = 0;
        let monthlyTransferProfit = 0;

        visibleDaily.forEach((tx: any) => {
          const txDate = new Date(tx.createdAt);
          const status = String(tx.status || 'confirmed').toLowerCase();
          if (txDate.toDateString() !== today.toDateString()) return;
          if (status !== 'completed' && status !== 'confirmed' && status !== 'pending') return;

          const t = (tx.type || tx.txnType || '').toLowerCase();
          const txForCalc = {
            type: t === 'withdrawal' ? 'withdraw' : t,
            amount: Number(tx.amount || 0),
            commission: tx.commission,
          };

          if (txForCalc.type === 'withdraw') {
            dailyWithdrawProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          } else if (txForCalc.type === 'send' || txForCalc.type === 'receive' || txForCalc.type === 'transfer') {
            dailyTransferProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          }
        });

        visibleMonthly.forEach((tx: any) => {
          const txDate = new Date(tx.createdAt);
          const status = String(tx.status || 'confirmed').toLowerCase();
          if (txDate.getMonth() !== currentMonth || txDate.getFullYear() !== currentYear) return;
          if (status !== 'completed' && status !== 'confirmed' && status !== 'pending') return;

          const t = (tx.type || tx.txnType || '').toLowerCase();
          const txForCalc = {
            type: t === 'withdrawal' ? 'withdraw' : t,
            amount: Number(tx.amount || 0),
            commission: tx.commission,
          };

          if (txForCalc.type === 'withdraw') {
            monthlyWithdrawProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          } else if (txForCalc.type === 'send' || txForCalc.type === 'receive' || txForCalc.type === 'transfer') {
            monthlyTransferProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          }
        });

        setTotals({
          dailyWithdraw: Math.round(dailyWithdrawProfit * 100) / 100,
          dailyTransfer: Math.round(dailyTransferProfit * 100) / 100,
          monthlyWithdraw: Math.round(monthlyWithdrawProfit * 100) / 100,
          monthlyTransfer: Math.round(monthlyTransferProfit * 100) / 100,
          lastUpdated: new Date(),
        });
        return;
      }

      const profits = ProfitService.getWithdrawTransferProfits(userId);
      setTotals({
        dailyWithdraw: profits.dailyWithdrawProfit || 0,
        dailyTransfer: profits.dailyTransferProfit || 0,
        monthlyWithdraw: profits.monthlyWithdrawProfit || 0,
        monthlyTransfer: profits.monthlyTransferProfit || 0,
        lastUpdated: new Date(),
      });
    } catch (e) {
      console.error('Error loading total profits:', e);
    }
  };

  return (
    <>
      <Button
        size="sm"
        variant="ghost"
        onClick={handleEyeClick}
        className="h-2 w-2 p-0 bg-gradient-to-r from-blue-100 to-purple-200 text-purple-700 hover:from-blue-200 hover:to-purple-300 hover:text-purple-800 rounded transition-all duration-300 hover:scale-110 shadow-sm hover:shadow-md"
        title="  "
      >
        <Eye className="h-1.5 w-1.5" />
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="max-w-[240px] mx-auto bg-white dark:bg-[#0f172a] border border-gray-200 dark:border-slate-800 shadow-md rounded sm:max-w-[220px] sm:bg-gradient-to-br sm:from-purple-50 sm:to-white sm:dark:from-slate-900 sm:dark:to-slate-800 sm:border-2 sm:border-purple-200 sm:dark:border-slate-700 sm:shadow-2xl sm:rounded-2xl">
          <DialogHeader className="text-center pb-0 border-b-0 sm:pb-2 sm:border-b sm:border-purple-200 dark:border-slate-700">
            <DialogTitle className="text-xs font-bold text-purple-800 dark:text-purple-300 flex items-center justify-center gap-1 mb-0 leading-tight tracking-tight sm:text-xs sm:gap-1 sm:mb-0 sm:leading-normal sm:tracking-normal">
              <DollarSign className="h-2 w-2 text-purple-600 dark:text-purple-400 sm:h-3 sm:w-3" />
                ()
            </DialogTitle>
          </DialogHeader>

          <div className="space-y-1 py-1 sm:space-y-2 sm:py-2">
            {/*   */}
            <div className="bg-blue-50 dark:bg-blue-900/20 p-1 rounded border border-blue-200 dark:border-blue-900/30 sm:bg-gradient-to-r sm:from-blue-50 sm:to-blue-100 sm:dark:from-blue-900/20 sm:dark:to-blue-900/10 sm:p-2 sm:rounded-xl sm:shadow-sm">
              <div className="flex items-center justify-between mb-1">
                <div className="flex items-center gap-1">
                  <Clock className="h-2 w-2 text-blue-600 dark:text-blue-400" />
                  <span className="text-[10px] font-semibold text-blue-800 dark:text-blue-300"></span>
                </div>
                <span className="text-[10px] font-bold text-blue-900 dark:text-blue-100">
                  {formatCurrency((totals.dailyWithdraw ?? 0) + (totals.dailyTransfer ?? 0))}
                </span>
              </div>
              <div className="grid grid-cols-2 gap-1">
                <div className="flex items-center justify-between bg-red-50 dark:bg-red-900/20 p-0.5 rounded border border-red-200 dark:border-red-900/30">
                  <div className="flex items-center gap-1">
                    <ArrowDownLeft className="h-2 w-2 text-red-600 dark:text-red-400" />
                    <span className="text-[10px] text-red-800 dark:text-red-300"></span>
                  </div>
                  <span className="text-[10px] font-bold text-red-700 dark:text-red-200">{formatCurrency(totals.dailyWithdraw)}</span>
                </div>
                <div className="flex items-center justify-between bg-green-50 dark:bg-green-900/20 p-0.5 rounded border border-green-200 dark:border-green-900/30">
                  <div className="flex items-center gap-1">
                    <ArrowRightLeft className="h-2 w-2 text-green-600 dark:text-green-400" />
                    <span className="text-[10px] text-green-800 dark:text-green-300"></span>
                  </div>
                  <span className="text-[10px] font-bold text-green-700 dark:text-green-200">{formatCurrency(totals.dailyTransfer)}</span>
                </div>
              </div>
            </div>

            {/*   */}
            <div className="bg-purple-50 dark:bg-purple-900/20 p-1 rounded border border-purple-200 dark:border-purple-900/30 sm:bg-gradient-to-r sm:from-purple-50 sm:to-purple-100 sm:dark:from-purple-900/20 sm:dark:to-purple-900/10 sm:p-2 sm:rounded-xl sm:shadow-sm">
              <div className="flex items-center justify-between mb-1">
                <div className="flex items-center gap-1">
                  <Calendar className="h-2 w-2 text-purple-600 dark:text-purple-400" />
                  <span className="text-[10px] font-semibold text-purple-800 dark:text-purple-300"></span>
                </div>
                <span className="text-[10px] font-bold text-purple-900 dark:text-purple-100">
                  {formatCurrency((totals.monthlyWithdraw ?? 0) + (totals.monthlyTransfer ?? 0))}
                </span>
              </div>
              <div className="grid grid-cols-2 gap-1">
                <div className="flex items-center justify-between bg-red-50 dark:bg-red-900/20 p-0.5 rounded border border-red-200 dark:border-red-900/30">
                  <div className="flex items-center gap-1">
                    <ArrowDownLeft className="h-2 w-2 text-red-600 dark:text-red-400" />
                    <span className="text-[10px] text-red-800 dark:text-red-300"></span>
                  </div>
                  <span className="text-[10px] font-bold text-red-700 dark:text-red-200">{formatCurrency(totals.monthlyWithdraw)}</span>
                </div>
                <div className="flex items-center justify-between bg-blue-50 dark:bg-blue-900/20 p-0.5 rounded border border-blue-200 dark:border-blue-900/30">
                  <div className="flex items-center gap-1">
                    <ArrowRightLeft className="h-2 w-2 text-blue-600 dark:text-blue-400" />
                    <span className="text-[10px] text-blue-800 dark:text-blue-300"></span>
                  </div>
                  <span className="text-[10px] font-bold text-blue-700 dark:text-blue-200">{formatCurrency(totals.monthlyTransfer)}</span>
                </div>
              </div>
            </div>
          </div>

          {!isAuthenticated && (
            <div className="absolute inset-0 bg-yellow-200/70 backdrop-blur-sm flex items-center justify-center rounded sm:rounded-2xl border border-yellow-300 pointer-events-auto">
              <Button
                size="sm"
                variant="ghost"
                onClick={() => setIsMasterPinDialogOpen(true)}
                className="p-2 bg-yellow-300/80 text-yellow-900 hover:bg-yellow-400/90 rounded-full shadow-sm hover:shadow-md transition-all duration-300"
                title="   "
              >
                <Eye className="h-4 w-4" />
              </Button>
            </div>
          )}

          <div className="flex justify-center pt-1 border-t-0 sm:pt-2 sm:border-t sm:border-purple-200">
            <Button
              onClick={() => setIsOpen(false)}
              className="bg-purple-600 hover:bg-purple-700 text-white px-4 py-1 rounded-xl text-xs sm:bg-gradient-to-r sm:from-purple-600 sm:to-purple-700 sm:hover:from-purple-700 sm:hover:to-purple-800 sm:px-4 sm:py-1 sm:rounded-xl sm:shadow-lg sm:hover:shadow-xl sm:transition-all sm:duration-300"
            >
              
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <MasterPinDialog
        open={isMasterPinDialogOpen}
        onOpenChange={setIsMasterPinDialogOpen}
        onSuccess={handlePinSuccess}
        description="          "
      />
    </>
  );
};

export const TotalProfitEyeButton: React.FC<TotalProfitDisplayDialogProps> = (props) => {
  return <TotalProfitDisplayDialog {...props} />;
};

export default TotalProfitDisplayDialog;
